.section {
  background-color: #fff;
  padding: 15px;
  margin-bottom: 10px;
  border-radius: 10px;
}

.rounded-corner {
  border-radius: 10px;
}


@media(max-width:500px){
  .content-table{
      box-shadow: none;
      overflow: unset;
  }
  .content-table thead{
      display: none;
  }
  .content-table, .content-table tbody, .content-table tr, .content-table td{
      display: block;
      width: 100%;
      
  }
  .content-table tr{
      margin-bottom: 15px;
      border-bottom: 2px solid #009879 !important;
  }
  .content-table td{
      text-align: right;
      padding-left: 50%;
      position: relative;
  }
  .content-table td::before{
      content: attr(data-labe);
      position: absolute;
      left: 0;
      width: 50%;
      padding-left: 15px;
      font-size: 15px;
      font-weight: bold;
      text-align: left;
  }
  .day{
      background-color: #009879;
      color: #ffffff;
  }
  h1 span{
    font-size: 40px;
  }
  button{
    padding: 15px 20px;
  }

  .break:after {
    content: '- Break' !important;
  }
}

